.carousel {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.inner {
  white-space: nowrap;
  transition: transform 0.3s;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.carousel-item {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.carousel-img {
  width: 100%;
  border-radius: 60px;
}

/* final stage */

.carousel-buttons {
  display: flex;
  justify-content: center;
}

.button-arrow {
  background: none;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}

.indicators {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}

.indicator-buttons {
  border: none;
  cursor: pointer;
}

.indicators > button {
  margin: 5px;
  background: none;
}

.indicator-symbol {
  color: #26343f;
}

.indicator-symbol-active {
  color: #ffffff;
}

.material-symbols-outlined{
  display: block;
  padding: 25px;
  margin: 0px 10px;
  background: url('../images/arrow-rigth.svg') no-repeat;
}

.left{
  transform: rotate(180deg);
}

.not-active{
  opacity: 0.5;
}